body.login {
  @apply bg-dark flex flex-col justify-center items-center w-full h-svh;
}

#login {
  @apply max-w-sm w-full p-0;
}
#login #login_error {
  color: #000;
}
#login .message {
  color: #000;
}
#login h1 a {
  width: 153px;
  height: 23px;
  background-size: 153px 23px;
  margin-bottom: 30px;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  @apply shadow-none border-dark bg-dark mt-0 p-4;
}
#login form label {
  @apply font-neueMachina uppercase text-xs text-light mb-2;
}
#login form input#user_pass,
#login form input#user_login {
  @apply rounded-md w-full py-3 px-3 font-neueMachina text-dark text-base outline-none border-none;
}
#login form input#user_pass:focus-visible,
#login form input#user_login:focus-visible {
  @apply outline-red;
}
#login form input[type=submit] {
  @apply inline-block font-monumentExtend font-bold text-sm text-red border border-red uppercase py-3 px-6 transition-all duration-300 ease-in-out hover:bg-red hover:text-dark bg-transparent rounded-3xl;
}
#login #backtoblog, #login #nav {
  @apply font-neueMachina text-center;
}
#login #backtoblog a, #login #nav a {
  @apply font-neueMachina text-gray-400;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #000 !important;
}

.wpml-login-ls form#wpml-login-ls-form select#wpml-language-switcher-locales {
  @apply rounded-none border border-solid uppercase text-xs font-neueMachina transition-all duration-300 ease-in hover:bg-white;
}
.wpml-login-ls form#wpml-login-ls-form input.button {
  @apply rounded-none border border-solid uppercase text-xs font-neueMachina px-4 py-1 transition-all duration-300 ease-in hover:bg-white;
}